  height: 100%;
  left: 0;
  top: 0;
  background-color: var(--white) !important;
  opacity: 1 !important;
  z-index: 99999;
}
.page-loader::after {
  background: 0 0;
  content: "" !important;
  position: absolute;
  left: calc(50% - 25px) !important;
  top: calc(50% - 25px) !important;
  width: 50px !important;
  height: 50px !important;
  opacity: 1 !important;
  right: inherit !important;
  z-index: 9;
  text-align: center;
