  position: relative;
}
.lr-container-line::after, .lr-container-line::before {
  position: absolute;
  top: 0;
  margin-top: -150px;
  width: 1px;
  height: 180px;
  opacity: 0.5;
  content: "";
  background: linear-gradient(180deg, rgba(52, 60, 217, 0) 0%, #343cd9 100%);
}
.lr-container-line::before {
  left: 0;
  animation: scroll1 15s ease-out infinite;
}
.lr-container-line::after {
  right: 0;
  animation: scroll1 20s ease-out infinite;
}

