}
.list-style-03 li {
  position: relative;
  padding: 0 0 0 38px;
  margin: 0 0 9px 0;
}
.list-style-03 li:before {
  content: counter(li, decimal-leading-zero) ".";
  counter-increment: li;
  display: inline-block;
  position: absolute;
  left: 0;
}
