/* Shop sidebar */
.shop-sidebar .shop-filter {
  padding: 0;
  margin: 0;
  list-style: none;
}
.shop-sidebar .shop-filter li {
  position: relative;
  margin-bottom: 2px;
  padding: 0 25px;
}
.shop-sidebar .shop-filter li a {
