  top: 50%;
  opacity: 0;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.shop-sidebar .shop-filter li .item-qty {
