/* Toolbar wrapper */
.toolbar-wrapper a img {
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.toolbar-wrapper a:hover img {
  opacity: 1;
}
/* Shop lable */
.shop-image {
  position: relative;
}
.shop-image .lable {
  padding: 0 8px;
