  border: 1px solid transparent;
}
.product-image-thumb .swiper-slide.swiper-slide-thumb-active {
  border-color: var(--light-gray);
}
/* Single product thumb vertical */
.single-product-thumb .slider-vertical {
  width: 100%;
  height: 100%;
