}
/* Shop color */
.shop-color {
  list-style: none;
  padding: 0;
  display: flex;
}
.shop-color li {
  position: relative;
  display: inline-block;
  vertical-align: middle;
}
.shop-color li > input[type=radio] + label {
  display: flex;
}
.shop-color li > input[type=radio] + label span {
  height: 32px;
  width: 32px;
  border-radius: 100%;
  margin-right: 6px;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  cursor: pointer;
}
.shop-color li input[type=radio]:checked + label span:after {
  position: absolute;
  top: 2px;
  left: 2px;
  color: var(--white);
  height: 28px;
  width: 28px;
  border: 2px solid var(--white);
  border-radius: 100%;
  text-align: center;
  content: "";
  font-weight: 900;
}
/* Shop size */
.shop-size {
  list-style: none;
  padding: 0;
  display: flex;
}
.shop-size li {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  margin-right: 6px;
}
.shop-size li > input[type=radio] + label {
  display: flex;
}
.shop-size li > input[type=radio] + label span {
  height: 37px;
  width: 37px;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
