  cursor: pointer;
  border: 1px solid var(--extra-medium-gray);
  text-align: center;
  font-size: 12px;
  line-height: normal;
  color: var(--dark-gray);
  font-weight: 500;
}
.shop-size li input[type=radio]:checked + label span:after {
  position: absolute;
  top: -1px;
  left: -1px;
  height: 37px;
  width: 37px;
  border: 1px solid var(--dark-gray);
  border-radius: 100%;
  text-align: center;
  content: "";
}
/* Shop quantity  */
.quantity {
