  padding: 11px 20px;
  border-width: 1px;
}
/* Shop category style 02 */
.shop-category-02 .category-box {
  overflow: hidden;
  position: relative;
}
.shop-category-02 .category-box .category-title {
  padding: 0 15px;
  width: 100%;
}
.shop-category-02 .category-box img {
  -webkit-transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
}
.shop-category-02 .category-box .category-hover-content {
  position: absolute;
  left: 20px;
  top: 20px;
  height: calc(100% - 40px);
  width: calc(100% - 40px);
  opacity: 0;
  visibility: hidden;
  -webkit-transform: scale(0.9);
  transform: scale(0.9);
}
.shop-category-02 .category-box .category-hover-content a {
  opacity: 0;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  -webkit-transform: translateY(30px);
  transform: translateY(30px);
}
.shop-category-02 .category-box .category-hover-content a:hover {
  color: var(--dark-gray);
}
.shop-category-02 .category-box .category-hover-content .title {
  opacity: 0;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  -webkit-transform: scale(0.8);
  transform: scale(0.8);
}
.shop-category-02 .category-box:hover img {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
  opacity: 0.6;
}
.shop-category-02 .category-box:hover .category-hover-content {
  opacity: 1;
  visibility: visible;
  -webkit-transform: scale(1);
  transform: scale(1);
}
.shop-category-02 .category-box:hover .category-hover-content .title {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
  -webkit-transition-delay: 0.1s;
  transition-delay: 0.1s;
}
.shop-category-02 .category-box:hover .category-hover-content a {
  opacity: 1;
  -webkit-transform: translateY(0px);
  transform: translateY(0px);
  -webkit-transition-delay: 0.2s;
  transition-delay: 0.2s;
}
/* Shop banner style 01 */
.shop-banner-style-01 figure figcaption {
  opacity: 0;
  -webkit-transition: all 0.5s cubic-bezier(0.12, 0, 0.39, 0);
  transition: all 0.5s cubic-bezier(0.12, 0, 0.39, 0);
}
.shop-banner-style-01 figure figcaption .banner-subtitle, .shop-banner-style-01 figure figcaption .banner-title, .shop-banner-style-01 figure figcaption .banner-btn {
  opacity: 0;
  -webkit-transform: translate3d(0, 15px, 0);
  transform: translate3d(0, 15px, 0);
  -webkit-transition: all 1s cubic-bezier(0.2, 0, 0.3, 1);
  transition: all 1s cubic-bezier(0.2, 0, 0.3, 1);
}
.shop-banner-style-01 figure figcaption .banner-subtitle {
  opacity: 0;
  -webkit-transform: translate3d(0, 15px, 0);
  transform: translate3d(0, 15px, 0);
  -webkit-transition: all 1s cubic-bezier(0.2, 0, 0.3, 1);
  transition: all 1s cubic-bezier(0.2, 0, 0.3, 1);
}
.shop-banner-style-01 figure figcaption .banner-title {
  opacity: 0;
  -webkit-transform: translate3d(0, 15px, 0);
  transform: translate3d(0, 15px, 0);
  -webkit-transition: all 0.7s cubic-bezier(0.2, 0, 0.3, 1);
  transition: all 0.7s cubic-bezier(0.2, 0, 0.3, 1);
}
.shop-banner-style-01 figure figcaption .banner-btn {
  opacity: 0;
  -webkit-transform: translate3d(0, 15px, 0);
  transform: translate3d(0, 15px, 0);
  -webkit-transition: all 1s cubic-bezier(0.2, 0, 0.3, 1);
  transition: all 1s cubic-bezier(0.2, 0, 0.3, 1);
}
.shop-banner-style-01 figure:hover figcaption {
  opacity: 1;
}
.shop-banner-style-01 figure:hover figcaption .banner-subtitle {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  -webkit-transition: all 0.4s cubic-bezier(0.2, 0, 0.3, 1);
  transition: all 0.4s cubic-bezier(0.2, 0, 0.3, 1);
}
.shop-banner-style-01 figure:hover figcaption .banner-title {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  -webkit-transition: all 0.7s cubic-bezier(0.2, 0, 0.3, 1);
  transition: all 0.7s cubic-bezier(0.2, 0, 0.3, 1);
}
.shop-banner-style-01 figure:hover figcaption .banner-btn {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  -webkit-transition: all 1s cubic-bezier(0.2, 0, 0.3, 1);
  transition: all 1s cubic-bezier(0.2, 0, 0.3, 1);
}
